	module.exports = {"data":{"post":{"html":"<p><b>Update:</b> Fever is down even further, but the two blood values the doctors are interested are still up, indicating a medium-sized lung infection. He's taking in fluids well, and starting to eat some. Christopher is in a 'single room with parent's bed', so Mama is with him all the time. I'll be taking shifts as soon as I am certified healthy.</p>\n<hr />\nThe world does not only revolve around Christopher...</p>\n<p>Strange but True Dept.: I was amused to see in my <a href=\"http://www.ix.de/ct\">c't</a> (<i>the</i> German computer magazine) yesterday an anti-Linux ad for Windows 2000 featuring <a href=\"http://www.koehntopp.de/kris/msad.jpg\">mutant penguins</a>. (Text: \"An open operating system doesn't only have advantages\".) According to <a href=\"http://slashdot.org\">slashdot</a>, it's the <a href=\"http://slashdot.org/articles/00/10/21/1644248.shtml\">first Microsoft ad that directly targets Linux</a>.</p>\n<p>You Can Choose Your Friends but You Can't Shake Your In-Laws Dept: Last weekend, when the Expo 2000 enjoyed record numbers of visitors near the 400,000 mark, Mama's two sisters-in-law accidently met on the Expo grounds.</p>","fields":{"title":"Other news","date":"22 Oct 2000","url":"https://papascott.de/2000-10-22-other-news/"}}},"pathContext":{"slug":"/2000-10-22-other-news/","prev":{"fields":{"title":"Not any better","date":"21 Oct 2000","slug":"/2000-10-21-not-any-better/"}},"next":{"fields":{"title":"Just to see you again","date":"23 Oct 2000","slug":"/2000-10-23-just-to-see-you-again/"}}}}

